<template>
  <div id="app">
    <div class="header" v-bind:class="{ active: isActive , navbar:isScroll, visible:isSearchBarActive}">
      <span id="left" @click="back"><i><font-awesome-icon :icon="['fas', 'angle-left']" /></i> Notes</span>
      <span id="center">Notes</span>
      <span @click="clickButton()" class="save" v-bind:class="{ active: isKeyUp }"><i><font-awesome-icon :icon="['fas', 'save']" /></i> Done</span>
    </div>
    <Note :note="noteById" :isSaveButtonClick="isButtonClick" v-bind:class="{ active: isActive }" :note1="noteObject" :msg="msg"
      v-on:keyUpped="iskeyUpped" v-on:clickButton="realeseButton" v-on:noteEdit="saveNoteEdit"
      v-on:newNote="addNewNote" />
    <div class="body" v-bind:class="{active: isSearchBarActive}" @scroll="handleScroll" ref="body">
      <Search v-on:isSearchActive="isSearchActive" v-on:notesSearch="getNotesSearch" :allNotes="allNotes"/>
      <List v-on:passNote="getNoteById" :allNotes="renderNote()" v-bind:class="{active: isSearchBarActive}"
            v-on:passIdNoteRemove="removeNoteById"/>
    </div>
    <Footer v-on:addNote="buttonAddNoteClick" :numberNotes="numberNotes"/>
    <div class="hidden" v-if="noteEdit!= null">{{editNote(noteEdit)}}</div>
    <div class="hidden" v-if="newNote != null">{{addNewNote(newNote)}}</div>
  </div>
</template>

<script>
import List from './components/List.vue';
import Search from './components/Search.vue';
import Footer from './components/footer-fix-bottom.vue';
import Note from './components/Note.vue';
import axios from "axios";
export default {
  name: 'App',
  components: {
    List, Search, Footer, Note,
  },
  data: function(){
    return {
      allNotes: null,
      noteById: null,
      noteEdit: null,
      newNote: null,
      noteObject: null,
      notesSearch: null,
      numberNotes: 0,
      msg: '',
      isActive: false,
      isScroll: false,
      isSearchBarActive: false,
      isButtonClick: false,
      isKeyUp: false,
    }
  },
  methods:{
    loadPage() {
      return axios
        .get("http://localhost:8080/api/v1/notes")
        .then(response => {return response});

    },
    getNoteById(value){
      this.noteById = value;
      this.isActive = true;
      this.msg = "edit";
    },
    getNotesSearch(value){
      this.notesSearch = value
    },
    back(){
      if(this.isActive){
        this.isActive = false;
        this.isKeyUp = false;
      } 
    },
    iskeyUpped(){
      this.isKeyUp = true;
    },
    saveNoteEdit(value){
      this.noteEdit = value
    },
    buttonAddNoteClick(value){
      this.isActive = true;
      this.noteObject = value;
      this.msg = "add";

    },
    clickButton(){
      if(!this.isButtonClick) {
        this.isButtonClick = true;
        this.isKeyUp= false;}
    },
    realeseButton(){
      if(this.isButtonClick) this.isButtonClick = false;
    },
    handleScroll(){
      if(this.$refs["body"].scrollTop > 0) this.isScroll = true;
      else this.isScroll = false;
    },
    isSearchActive(value){
      this.isSearchBarActive = value;
    },
    editNote(va){
      axios
        .put(`http://localhost:8080/api/v1/notes/${va['id']}`, va)
        .then(response => {if(response.status == 200) this.forceRerender();})
        .catch(error => console.log(error));
      this.noteEdit = null;
    },
    addNewNote(va){
      axios
        .post("http://localhost:8080/api/v1/notes", va)
        .then(response => {if(response.status == 200) this.forceRerender();})
        .catch(error => console.log(error));
      this.newNote = null;
    },
    removeNoteById(value){
      let id = value;
      axios
        .delete(`http://localhost:8080/api/v1/notes/${id}`)
        .then(response => {if(response.status == 200) this.forceRerender();})
        .catch(error => console.log(error));
    },
    forceRerender() {
      this.loadPage().then(response => {
        if(response.status == 200){
          this.allNotes = response.data;
          this.numberNotes = this.allNotes.length;
        }
      });
    },
    renderNote(){
      if(this.isSearchBarActive){
        return this.notesSearch;
      }
      else{
        return this.allNotes;
      } 
    }
  },
  created () {
    window.addEventListener('scroll', this.handleScroll);
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll);
  },
  mounted() {
    this.forceRerender();
    
  },
}
</script>

