<template>
  <div class="searchNote" v-bind:class="{active: isActive}">
      <div class="container-search">
        <i><font-awesome-icon :icon="['fas', 'search']" /></i>
        <input class="search" type="text" placeholder="Search" v-model="strSearch" @keyup="searchNote(strSearch, allNotes)" 
        @focus="searchBarActive"
        @blur="searchBarNone">
        <i class="close" v-bind:class="{active: isStr}" @click="closeClick"><font-awesome-icon :icon="['fas', 'times-circle']" /></i>
      </div>
      <span class="cancel" v-bind:class="{active: isActive}" @click="cancelClick(allNotes)">Cancel</span>
  </div>
</template>

<script>
export default {

    
  props: {
      allNotes: Array,
  }, 
  data: function() {
      return {
          strSearch: "",
          isActive: false,
          isStr: false,
          notesSearch: [],
      }
  },
  methods: {
      searchNote(strSearch, allNotes){
        if(strSearch != '') this.isStr = true;
        else this.isStr = false;
        this.notesSearch = [];
        let regexArray = new RegExp(strSearch
                                        .trim()
                                        .toLowerCase()
                                        .split(' ')
                                        .join("|"), 'gi');
        for(let index in allNotes){
            if(regexArray.test(allNotes[index].name) || regexArray.test(allNotes[index].description)) this.notesSearch.push(allNotes[index]);
        }
        this.$emit("notesSearch", this.notesSearch);
      },
      searchBarActive(){
        this.isActive = true;
        this.$emit("isSearchActive", true);
      },
      searchBarNone(){
        if(this.strSearch != "") this.$emit("isSearchActive", true);
        else {
            this.isActive = false;
            this.$emit("isSearchActive", false);
        }
      },
      cancelClick(allNotes){
        this.strSearch = "";
        this.isStr = false;
        this.$emit("notesSearch", allNotes);
        this.searchBarNone()
      },
      closeClick(){
        this.strSearch = '';
        this.isStr = false;
        this.$emit("notesSearch", this.allNotes);
      }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .searchNote{
        background: none;
        margin: 20px 0;
        display: flex;
        align-items: center;
        padding: 0 18px;
        transition: 0.4s;
        box-sizing: border-box;
    }
    .container-search{
        display: flex;
        align-items: center;
        width: 100%;
        background: rgb(53, 53, 53);
        padding: 0 10px;
        border-radius: 8px;
        transition: 0.4s ease-in;
        
    }
    .searchNote.active{
        background: rgb(49, 49, 49, 0.8) ;
        padding-top: 50px;
        padding-bottom: 10px;
        backdrop-filter: blur(10px);
        margin-top:0 ;
        position: fixed;
        width: 100%;
    }
    input{
        background: none;
        border: none;
        margin: 0px 10px;
        color: white;
        padding: 10px 0;
        width: 100%;
        outline: none;
        font-size: 14px;
    }
    input::placeholder{
        color: rgba(211, 211, 211, 0.5);
    }
    i{
        color: rgba(211, 211, 211, 0.5);;
    }
    .searchNote.active .container-search{
        margin-right:10px;
    }
    .cancel{
        color:rgb(255, 208, 0);
        width: 0;
        opacity: 0;
        transition: 0.4s;
        text-align: center;
    }
    .cancel:hover{
        cursor: pointer;
        color: rgb(255, 208, 0, 0.5);
    }
    .cancel.active{
        width: 6%;
        opacity: 1;
    }
    .close{
        opacity: 0;
    }
    .close.active{
        opacity: 1;
    }
</style>
