<template>
  <div class="container-note" > 
    <div class="note-id" v-if="msg!= ''">
        <input class="name" type="text" v-if="checkMsg(note, msg) == 2" v-model="note.name" @keyup="onKeyPress()">
        <textarea class="description" v-if="checkMsg(note, msg) == 2" v-model="note.description" @keyup="onKeyPress()"></textarea>
        <input class="name" type="text" v-if="checkMsg(note1, msg) == 1" v-model="note1.name" @keyup="onKeyPress()">
        <textarea class="description" v-if="checkMsg(note1, msg) == 1" v-model="note1.description" @keyup="onKeyPress()"></textarea>
        <div class="hidden" v-if="isSaveButtonClick == true && msg=='edit'">{{passNoteEdit(note)}}</div>
        <div class="hidden" v-if="isSaveButtonClick == true && msg=='add'">{{passNewNote(note1)}}</div>
    </div>
</div>
</template>

<script>


export default {
  data: function(){
      return {
          noteEdit: {
              id: 0,
              name: "",
              description: ""
          },
          newNote:{
              name:"",
              description:""
          },
          
      }
  },  
  props:{
      note: Object,
      note1: Object,
      isSaveButtonClick: Boolean,
      msg: String,
  },
  methods: {
      onKeyPress(){
          this.$emit("keyUpped");
      },
      passNoteEdit(note){
          this.noteEdit['id'] = note.id;
          this.noteEdit['name'] = note.name;
          this.noteEdit['description'] = note.description;
          this.$emit("noteEdit", this.noteEdit);
          this.$emit("clickButton")
      },
      passNewNote(note){
          this.newNote['name'] = note.name;
          this.newNote['description'] = note.description;
          this.$emit("newNote", this.newNote);
          this.$emit("clickButton")
      },
      checkMsg(value, msg){
          if(value != null){
              if(msg == 'add') return 1;
              else if(msg == 'edit') return 2;
          } return 0;
      }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.container-note{
  background: black;
  height: 100%;
  max-height: 100vh;
  width: 100%;
  z-index: 1;
  transition: 0.4s cubic-bezier(0,.0,1,1);
  transform: translate(100%, 0);
  visibility: hidden;
  overflow: hidden;
  position: absolute;
}
.container-note.active{
    transform: translate(0%, 0);
    min-height: 92vh;
    visibility: visible;
}
.note-id{
    padding: 80px 29px;
}
.name{
    font-size: 30px;
    font-weight: bold;
    background: none;
    border: none;
    outline: none;
    color: white;
    width: 100%;
}
.description{
    font-weight: lighter;
    background: none;
    color: white;
    border: none;
    width: 100%;
    height: 85vh;
    margin: 20px 0;
    font-size: 20px;
    outline: none;
    font-family: Avenir, Helvetica, Arial, sans-serif;
}
</style>
