<template>

  <div class="listNotes">
    <div class="container" v-for="(item, index) in allNotes" :key="index">
      <div class="note" v-bind:class="{transition: isTrans}" :ref="'element'" @mousedown="dragStart($event, index)" 
      @mousemove="drag($event, index)" @mouseup="dragEnd(item.id, index)">
        <div class="name">{{ item.name }}</div>
      </div>
      <div class="remove" v-bind:class="{transition: isTrans}" :ref="'remove'" @click="removeNoteById(item.id, index)">
        <span><i><font-awesome-icon :icon="['fas', 'trash-alt']" /></i></span>
        </div>
    </div>
  </div>
</template>

<script>
export default {
  
  name: "List",
  props:{
    noteEdit: Object,
    newNote: Object,
    allNotes: Array,
  },
  data: function () {
    return {
      noteById: null,
      numberNotes: 0,
      msg: "",
      isKeyUp: true,
      isDrag: false,
      coordinatate:null,
      isTrans: false,
    };

  },
  methods: {
    getNoteById(id){
      this.noteById = this.allNotes.find(item => item.id == id)
      this.$emit('passNote', this.noteById)
    },
    removeNoteById(id, index){
      this.$emit('passIdNoteRemove', id);
      let dragItem = this.$refs.element[index];
      let removeDiv = this.$refs.remove[index];
      removeDiv.children[0].style.marginRight = "-16px";
      this.isTrans = true;
      removeDiv.style.width= '0px';
      dragItem.xOffset = 0;
    },
    dragStart(event, index) {
      this.isTrans = false;
      this.coordinatate = this.$refs.element[index];
      if(this.coordinatate !== null){
        if (!this.coordinatate.xOffset) {
            this.coordinatate.xOffset = 0;
        }
        this.coordinatate.initialX = event.clientX - this.coordinatate.xOffset;
        this.isKeyUp = false;
      }
    },

    dragEnd(id, index) {
      this.isKeyUp = true;
      if(this.coordinatate !== null){
        if(this.isDrag){
        this.isDrag = false;
        let dragItem = this.$refs.element[index];
        let removeDiv = this.$refs.remove[index];
        if(dragItem.currentX + dragItem.xOffset < -100){

          removeDiv.style.width= 51.3+'px';
          dragItem.xOffset = -51.3;
          this.isTrans = true;
        } else{
          removeDiv.children[0].style.marginRight = "-16px";
          this.isTrans = true;
          removeDiv.style.width= '0px';
          dragItem.xOffset = 0;
        }
        this.coordinatate.style.marginLeft = '0px'
        
      } 
      else this.getNoteById(id);
      }
    this.coordinatate = null;
    },

    drag(event, index) {
      if(this.isKeyUp) this.isDrag = false;
      else this.isDrag = true;
      if (this.isDrag) {
        event.preventDefault();
        this.coordinatate.currentX = event.clientX - this.coordinatate.initialX;
        let removeDiv = this.$refs.remove[index];
        if(this.coordinatate.currentX < 0){
          this.coordinatate.style.marginLeft = this.coordinatate.currentX+'px';
          if(-16 - this.coordinatate.currentX < 0)
            removeDiv.children[0].style.marginRight = -16 - this.coordinatate.currentX+'px';
          else {
            removeDiv.children[0].style.marginRight = "0px";
          }
          removeDiv.style.width= -this.coordinatate.currentX+'px';
        }
        else {
          removeDiv.style.width = '0px';
          removeDiv.children[0].style.marginRight = "-16px";
        }
      }
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.listNotes{
  background-color: rgb(53, 53, 53);
  border-radius: 8px;
  margin: auto 18px;
  overflow-x: hidden;
}
.note {
  padding: 0 36px;
  width: 100%;
}
.note:hover{
  background: rgb(70,70,70);
}
.name {
  font-size: 20px;
  font-weight: bold;
  color: white;
  border-bottom: 1px solid rgba(211, 211, 211, 0.223) ;
  padding: 10px 0;
}
.container:last-child .name{
  border-bottom: none;
}
.remove{
  background: red;
  height: 44.2px;
  width: 0px;
  align-items: center;
  display: flex;
  font-size: 18px;
  justify-content: center;
}
.remove:hover{
  cursor: pointer;
  background: rgba(255, 0, 0, 0.8);
}
.remove:hover span{
  color: rgba(255, 255, 255, 0.9);
}
.remove span{
  margin-right: -16px;
}
.transition{
  transition: 0.4s;
}
.container{
  display: flex;
  
}
.span{
  align-items: center;
}
</style>
